html,
body {
  width: 100%;
  height: 100%;
}

body {
  font-family: 'Muli', 'Helvetica', 'Arial', 'sans-serif';
}

a {
  color: #fdcc52;
  -webkit-transition: all .35s;
  -o-transition: all .35s;
  transition: all .35s;
}

a:hover, a:focus {
  color: #fcbd20;
}

hr {
  max-width: 100px;
  margin: 25px auto 0;
  border-width: 1px;
  border-color: rgba(34, 34, 34, 0.1);
}

hr.light {
  border-color: white;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: 'Catamaran', 'Helvetica', 'Arial', 'sans-serif';
  font-weight: 200;
  letter-spacing: 1px;
}

p {
  font-size: 18px;
  line-height: 1.5;
  margin-bottom: 20px;
}


#mainNav {
  border-color: rgba(34, 34, 34, 0.05);
  background-color: white;
  -webkit-transition: all .35s;
  -o-transition: all .35s;
  transition: all .35s;
  font-family: 'Catamaran', 'Helvetica', 'Arial', 'sans-serif';
  font-weight: 200;
  letter-spacing: 1px;
}

#mainNav .navbar-brand {
  color: #fdcc52;
  font-family: 'Catamaran', 'Helvetica', 'Arial', 'sans-serif';
  font-weight: 200;
  letter-spacing: 1px;
}

#mainNav .navbar-brand:hover, #mainNav .navbar-brand:focus {
  color: #fcbd20;
}

#mainNav .navbar-toggler {
  font-size: 12px;
  padding: 8px 10px;
  color: #222222;
}

#mainNav .navbar-nav > li > a {
  font-size: 11px;
  font-family: 'Lato', 'Helvetica', 'Arial', 'sans-serif';
  letter-spacing: 2px;
  text-transform: uppercase;
}

#mainNav .navbar-nav > li > a.active {
  color: #fdcc52 !important;
  background-color: black;
}

#mainNav .navbar-nav > li > a.active:hover {
  background-color: black;
  border-color: white;
}

#mainNav .navbar-nav > li > a,
#mainNav .navbar-nav > li > a:focus {
  color: #222222;
}

#mainNav .navbar-nav > li > a:hover,
#mainNav .navbar-nav > li > a:focus:hover {
  color: #fdcc52;
}

@media (min-width: 992px) {
  #mainNav {
    border-color: transparent;
    background-color: black;
  }
  #mainNav .navbar-brand {
    color: fade(white, 70%);
  }
  #mainNav .navbar-brand:hover, #mainNav .navbar-brand:focus {
    color: white;
  }
  #mainNav .navbar-nav > li > a,
  #mainNav .navbar-nav > li > a:focus {
    color: rgba(255, 255, 255, 0.7);
  }
  #mainNav .navbar-nav > li > a:hover,
  #mainNav .navbar-nav > li > a:focus:hover {
    color: white;
  }
  #mainNav.navbar-shrink {
    border-color: rgba(34, 34, 34, 0.1);
    background-color: white;
  }
  #mainNav.navbar-shrink .navbar-brand {
    color: #222222;
  }
  #mainNav.navbar-shrink .navbar-brand:hover, #mainNav.navbar-shrink .navbar-brand:focus {
    color: #fdcc52;
  }
  #mainNav.navbar-shrink .navbar-nav > li > a,
  #mainNav.navbar-shrink .navbar-nav > li > a:focus {
    color: #222222;
  }
  #mainNav.navbar-shrink .navbar-nav > li > a:hover,
  #mainNav.navbar-shrink .navbar-nav > li > a:focus:hover {
    color: #fdcc52;
  }
}

.masthead {
  position: relative;
  width: 100%;
  max-height: 25%;
  padding-top: 150px;
  padding-bottom: 100px;
  color: white;
  background: url("../../img/bg-pattern.png"), #7b4397;
  background: url("../../img/bg-pattern.png"), -webkit-linear-gradient(to left, #7b4397, #dc2430);
  background: url("../../img/bg-pattern.png"), -webkit-gradient(linear, right top, left top, from(#7b4397), to(#dc2430));
  background: url("../../img/bg-pattern.png"), -webkit-linear-gradient(right, #7b4397, #dc2430);
  background: url("../../img/bg-pattern.png"), -o-linear-gradient(right, #7b4397, #dc2430);
  background: url("../../img/bg-pattern.png"), linear-gradient(to left, #7b4397, #dc2430);
}

.masthead .header-content {
  max-width: 500px;
  margin-bottom: 100px;
  text-align: center;
}

.masthead .header-content h1 {
  font-size: 30px;
}

.masthead .device-container {
  max-width: 325px;
  margin-right: auto;
  margin-left: auto;
}

.masthead .device-container .screen img {
  border-radius: 3px;
}

@media (min-width: 992px) {
 .masthead {
    height: 100vh;
    min-height: 300px;
    max-height: 300px;
    padding-top: 0;
    padding-bottom: 0;
  }
 .masthead .header-content {
    margin-bottom: 0;
    text-align: left;
  }
  .masthead .header-content h1 {
    font-size: 50px;
  }
  .masthead .device-container {
    max-width: 325px;
  }
}

.download {
  position: relative;
  padding: 150px 0;
}

.download h2 {
  font-size: 50px;
  margin-top: 0;
}

.download .badges .badge-link {
  display: block;
  margin-bottom: 25px;
}

.download .badges .badge-link:last-child {
  margin-bottom: 0;
}

.download .badges .badge-link img {
  height: 60px;
}

@media (min-width: 768px) {
  .download .badges .badge-link {
    display: inline-block;
    margin-bottom: 0;
  }
}

@media (min-width: 768px) {
  .download h2 {
    font-size: 70px;
  }
}

.features .-heading {
  margin-bottom: 100px;
}

.features .-heading h2 {
  margin-top: 0;
}

.features .-heading p {
  margin-bottom: 0;
}

.features .device-container,
.features .feature-item {
  max-width: 325px;
  margin: 0 auto;
}

.features .device-container {
  margin-bottom: 100px;
}

@media (min-width: 992px) {
  .features .device-container {
    margin-bottom: 0;
  }
}

.features .feature-item {
  padding-top: 50px;
  padding-bottom: 50px;
  text-align: center;
}

.features .feature-item h3 {
  font-size: 30px;
}

.features .feature-item i {
  font-size: 80px;
  display: block;
  margin-bottom: 15px;
  background: -webkit-linear-gradient(to left, #7b4397, #dc2430);
  background: -webkit-gradient(linear, right top, left top, from(#7b4397), to(#dc2430));
  background: -webkit-linear-gradient(right, #7b4397, #dc2430);
  background: -o-linear-gradient(right, #7b4397, #dc2430);
  background: linear-gradient(to left, #7b4397, #dc2430);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.cta {
  position: relative;
  padding: 250px 0;
  /* background-image: url("../../img/bg-cta.jpg"); */
  background-position: center;
  background-size: cover;
}

.cta .cta-content {
  position: relative;
  z-index: 1;
}

.cta .cta-content h2 {
  font-size: 50px;
  max-width: 450px;
  margin-top: 0;
  margin-bottom: 25px;
  color: white;
}

@media (min-width: 768px) {
  .cta .cta-content h2 {
    font-size: 80px;
  }
}

.cta .overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
}

.contact {
  text-align: center;
}

.contact h2 {
  margin-top: 0;
  margin-bottom: 25px;
}

.contact h2 i {
  color: #dd4b39;
}

.contact ul.list-social {
  margin-bottom: 0;
}

.contact ul.list-social li a {
  font-size: 40px;
  line-height: 80px;
  display: block;
  width: 80px;
  height: 80px;
  color: white;
  border-radius: 100%;
}

.contact ul.list-social li.social-twitter a {
  background-color: #1da1f2;
}

.contact ul.list-social li.social-twitter a:hover {
  background-color: #0d95e8;
}

.contact ul.list-social li.social-facebook a {
  background-color: #3b5998;
}

.contact ul.list-social li.social-facebook a:hover {
  background-color: #344e86;
}

.contact ul.list-social li.social-google-plus a {
  background-color: #dd4b39;
}

.contact ul.list-social li.social-google-plus a:hover {
  background-color: #d73925;
}

footer {
  padding: 25px 0;
  text-align: center;
  color: rgba(255, 255, 255, 0.3);
  background-color: #222222;
}

footer p {
  font-size: 12px;
  margin: 0;
}

footer ul {
  margin-bottom: 0;
}

footer ul li a {
  font-size: 12px;
  color: rgba(255, 255, 255, 0.3);
}

footer ul li a:hover, footer ul li a:focus, footer ul li a:active, footer ul li a.active {
  text-decoration: none;
}

.bg-primary {
  background: #fdcc52;
  background: -webkit-linear-gradient(#fdcc52, #fdc539);
  background: -webkit-gradient(linear, left top, left bottom, from(#fdcc52), to(#fdc539));
  background: -o-linear-gradient(#fdcc52, #fdc539);
  background: linear-gradient(#fdcc52, #fdc539);
}

.text-primary {
  color: #fdcc52;
}

.no-gutter > [class*='col-'] {
  padding-right: 0;
  padding-left: 0;
}

.btn-outline {
  color: white;
  border: 1px solid;
  border-color: white;
}

.btn-outline:hover, .btn-outline:focus, .btn-outline:active, .btn-outline.active {
  color: white;
  border-color: #fdcc52;
  background-color: #fdcc52;
}

.btn {
  border-radius: 300px;
  font-family: 'Lato', 'Helvetica', 'Arial', 'sans-serif';
  letter-spacing: 2px;
  text-transform: uppercase;
}

.btn-xl {
  font-size: 11px;
  padding: 15px 45px;
}

.mastimg
{
  min-width: 150%;
  margin-left: 0px;
  margin-top: 30%;
  border-radius: 8px;
}
.header-content{
  margin-top: 10%;
}

.txtcol{
  margin-left: -100px;
  margin-top: 150px;
}

.txtimg{
  margin-left: -100px;
  margin-top: 150px;
}

.wbhdr{

  margin-left: 10%;
}


